import { JSONSchema6 } from "json-schema";
import React from "react";
import { UiSchema } from "react-jsonschema-form";
import { ITextProps } from "../types";

const defaultProps: ITextProps = {
  text: "",
  textAlign: "center",
  verticalAlign: "center"
};

export default defaultProps;

export const Text: React.FC<ITextProps> = options => {
  const props = { ...defaultProps, ...options };
  return (
    <div
      style={{
        width: "100%",
        height: "100%",
        display: "flex",
        justifyContent: props.textAlign,
        alignItems: props.verticalAlign
      }}
    >
      <span>{props.text}</span>
    </div>
  );
};

export const TextPropsSchema: JSONSchema6 = {
  properties: {
    text: { type: "string", title: "" },
    textAlign: {
      type: "string",
      title: "",
      enum: [
        { label: "", value: "flex-start" },
        { label: "", value: "center" },
        { label: "", value: "flex-end" }
      ]
    },
    verticalAlign: {
      type: "string",
      title: "",
      enum: [
        { label: "", value: "flex-start" },
        { label: "", value: "center" },
        { label: "", value: "flex-end" }
      ]
    }
  }
};

export const TextPropsUiSchema: { [k in keyof ITextProps]: UiSchema } = {
  text: {
    "ui:widget": "StringInput",
    "ui:options": { span: 24, labelFixed: 100, size: "small" }
  },
  textAlign: {
    "ui:widget": "Select",
    "ui:options": { span: 24, labelFixed: 100, size: "small" }
  },
  verticalAlign: {
    "ui:widget": "Select",
    "ui:options": { span: 24, labelFixed: 100, size: "small" }
  }
};
